import styled from 'styled-components';

const IssueStyles = styled.li`
  border: 1px solid #ccc;
  border-radius: 0.25rem;
  display: flex;
  align-items: center;
  padding: 1rem;
  margin: 1rem;
  cursor: move;

  .issue-details {
    display: flex;
    flex-direction: column;
    margin-left: 0.5rem;
    overflow: hidden;

    h2 {
      font-weight: 700;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
`;

export default IssueStyles;
